<template>
    <li @click.stop="toggle();showChart(detial.auth);">
        {{detial.name}}
        <ul v-show="open" v-if="detial.node && detial.node.length > 0">
            <tree v-for="(item, inx) in detial.node" :key="inx" :detial="item"></tree>
        </ul>
    </li>
</template>
<script>
import tree from './tree'
export default {
    name: 'tree',
    props:['detial'],
    data (){
        return {
            open : false,
            userauth : 0,
        }
    },
    methods : {
        toggle() {
            if(this.detial.node && this.detial.node.length > 0) {
                this.open = !this.open;
            }
        },
        showChart(auth=0) {
            console.log('showChart => ' + auth);
            if(auth != 0) {
                this.$store.commit('setAuth', auth);
            }
        }
    }
}
</script>
<style scoped>
li {
    list-style-type: none;
}
    
</style>


