import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

// state 
const state = {
    user : 'HaHa',
    auth : null,
    channelList: [{channel: 'permission1', permission: 1, isshow: true},
        {channel: 'permission2', permission: 2, isshow: true},
        {channel: 'permission3', permission: 3, isshow: true}
    ],
    memberList: [
        {name: '小明', permission: 3},
        {name: '小美', permission: 2},
        {name: '阿傑', permission: 1},
        {name: '東東', permission: 2}
    ],
    
}

const mutations  = {
    loginUser(state, data) {
        console.log('data = ' ,data);
        state.user = data;
    },
    setAuth(state, data) {
        console.log('auth = ' ,data);
        state.auth = data;
    }
}

const actions = {
    callName (context, data) {
        setTimeout(() =>{
            context.commit('changMsg', data);
            console.log(data);
        }, 2000)
    }
}

export default new Vuex.Store ( {
    state,
    mutations,
    actions

} )


