<template>
    <div>
        <ul style="text-align: left;">
            <tree :detial="mytree"></tree>  
        </ul>
        <permission1 v-if="this.$store.state.auth==1 || this.$store.state.auth==3"></permission1>
        <permission2 v-if="this.$store.state.auth==2 || this.$store.state.auth==3"></permission2>
    </div>
</template>
<script>
import tree from './tree'
import permission1 from "./permission1";
import permission2 from "./permission2";
import permission3 from "./permission3";
export default {
    name: 'HelloWorld',
    components:{
        tree: tree,
        permission1: permission1,
        permission2: permission2,
        permission3: permission3
    },
    data (){
        return {
            mytree : {
                name: "一年C班",
                node: [
                        {
                            name: "A組",
                            node: [
                                {
                                    name: "小明",
                                    auth: 1,
                                },
                                {
                                    name: "小華",
                                    auth: 2,
                                },
                            ],
                        },
                    {
                        name: "B組",
                        node: [
                            {
                                name: "小智",
                                auth: 3,
                            },
                        ],
                    },
                ],
            },
        }
    } ,
}
</script>


